"use client";

import { useState } from "react";

export default function AddComments() {
  const [selectedUser, setSelectedUser] = useState("");
  const [comment, setComment] = useState("");

  const users = [
    { id: "1", name: "John Doe", company: "TechCorp Solutions" },
    { id: "2", name: "Sarah Johnson", company: "Innovate Technologies" },
    { id: "3", name: "Mike Chen", company: "WebDev Co." },
    { id: "4", name: "Emma Wilson", company: "StartupXYZ" },
    { id: "5", name: "David Rodriguez", company: "Creative Agency" },
    { id: "6", name: "Lisa Park", company: "Design Studio Pro" },
    { id: "7", name: "Alex Thompson", company: "CodeWorks Inc." },
    { id: "8", name: "Maria Garcia", company: "Digital Hub Solutions" },
  ];

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedUser || !comment.trim()) return;
    console.log("Comment submitted:", { selectedUser, comment });
    setComment("");
    setSelectedUser("");
  };

  return (
    <div className="rounded-lg border text-card-foreground shadow-sm p-6 bg-comment-bg border-comment-border">
      <div className="flex items-center gap-2 mb-4">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="w-5 h-5 text-primary"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          strokeWidth={2}
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <path d="M7.9 20A9 9 0 1 0 4 16.1L2 22Z" />
        </svg>
        <h3 className="text-lg font-semibold text-foreground">Add a Comment</h3>
      </div>

      <form onSubmit={handleSubmit} className="space-y-4">
       
        <div>
          <label className="block text-sm font-medium text-foreground mb-2">
            Select User
          </label>
          <select
            value={selectedUser}
            onChange={(e) => setSelectedUser(e.target.value)}
            className="w-full flex h-10 items-center rounded-md border border-input bg-background px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-ring"
          >
            <option value="">Choose a user to comment as...</option>
            {users.map((user) => (
              <option key={user.id} value={user.id}>
                {user.name} ({user.company})
              </option>
            ))}
          </select>
        </div>

      
        <div>
          <label className="block text-sm font-medium text-foreground mb-2">
            Your Comment
          </label>
          <textarea
            value={comment}
            onChange={(e) => setComment(e.target.value)}
            className="flex min-h-[80px] w-full rounded-md border border-input bg-background px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-ring resize-none"
            placeholder="Write your comment here..."
            rows={4}
          />
        </div>

       
        <button
          type="submit"
          disabled={!selectedUser || !comment.trim()}
          className="flex items-center justify-center gap-2 rounded-md bg-primary text-primary-foreground hover:bg-primary/90 h-10 px-4 py-2 text-sm font-medium disabled:opacity-50"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="w-4 h-4"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth={2}
            strokeLinecap="round"
            strokeLinejoin="round"
          >
            <path d="M14.536 21.686a.5.5 0 0 0 .937-.024l6.5-19a.496.496 0 0 0-.635-.635l-19 6.5a.5.5 0 0 0-.024.937l7.93 3.18a2 2 0 0 1 1.112 1.11z" />
            <path d="m21.854 2.147-10.94 10.939" />
          </svg>
          Post Comment
        </button>
      </form>
    </div>
  );
}
